import React from "react"
import Todo from "./Todo";

const TodoList = ({todos, remove, complete}) => {
    const todoNode = todos.map((todo, index) => {
      return (
      <React.Fragment key={Number(todo.id)}>
      <Todo todo={todo}  remove={remove} complete={complete}/>
      </React.Fragment> 
      )  
     })
       return (
        <div id="list">
          <p id="info"> Your Todos: </p>
          {todoNode}
        </div>
       ) 
  
}

export default TodoList